import Vue, { ComponentOptions } from 'vue';
import { Component, watch } from 'vue-property-decorator';
import socketIO from 'socket.io-client';
import './chat.scss';

@Component({
  template: require('./chat.html'),
  data() {
    return {
      activeRoom: undefined,
      userInput: undefined,
      activeRoomMessages: [],
      users: {},
      history: {}
    };
  }
})

export class ChatComponent extends Vue {
  socket: any = undefined;
  activeRoom: string = undefined;
  userInput: string = undefined;
  activeRoomMessages: any[] = [];
  history: any = {};
  users: any = {};
  changeActiveRoom(id) {
    this.activeRoom = id;
    this.history[id] = (this.history[id]) ? this.history[id] : [];
    this.activeRoomMessages = this.history[id];
  };
  mounted() {
    this.socket = socketIO({
      transportOptions: {
        polling: {
          extraHeaders: {
            'username': this.$store.state.user.username
          }
        }
      }
    });
    let self = this;
    console.log('   .');
    console.log('     extraHeader   ');
    console.log('          ', [{id: '', username: ''}, {id: '', username: ''}]);
    this.socket.on('history', function(id, data) {
      self.history[id] = data;
    });
    this.socket.on('all users', function(data) {
        self.users = data;
    });
    this.socket.on('new user', function(data) {
      let object = self.users;
      object[data.id] = data;
      self.users = [];
      self.users = object;
    });
    this.socket.on('delete user', function(data) {
      let object = self.users;
      delete object[data];
      self.users = [];
      self.users = object;
    });
    this.socket.on('chat message', function(msg, user){
      let obj = {
        text: msg,
        isMyMessage: false
      };
      if (!self.history[user]) {
        self.history[user] = [];
        self.history[user].push(obj);
        return;
      } else {
        self.history[user].push(obj);
        return;
      };
    });
  };
  beforeDestroy() {
    this.socket.disconnect();
  };
  sendMessage() {
    if (!this.userInput) {
      return;
    };
    console.log('SEND: ', this.userInput);
    let obj = {
      text: this.userInput,
      isMyMessage: true
    };
    if (!this.history[this.activeRoom]) {
      this.history[this.activeRoom] = [];
      this.history[this.activeRoom].push(obj);
    } else {
      this.history[this.activeRoom].push(obj);
    }
    this.socket.emit('chat message', this.userInput, this.activeRoom);
    this.userInput = '';
  }
}


