import Vue, { ComponentOptions } from 'vue';
import { Component, watch } from 'vue-property-decorator';

import './main.scss';

@Component({
  template: require('./main.html'),
  computed: {
    isRegNeeded() {
      return this.$store.state.isRegNeeded;
    },
    isAuth() {
      return this.$store.state.access_token;
    }
  },
  data() {
    return {
      username: undefined,
      password: undefined,
      secondPassword: undefined,
      firstName: undefined,
      surName: undefined,
      middleName: undefined,
      oldPassword: undefined,
      remembered: undefined
    };
  },
  methods: {
    toggleMode() {
      this.$store.commit('toggleMode');
    },
    logout() {
      this.$store.commit('logout');
    }
  }
})

export class MainComponent extends Vue {
    constructor () {
      super();
    };
    username: string = undefined;
    password: string = undefined;
    secondPassword: string = undefined;
    firstName: string = undefined;
    surName: string = undefined;
    middleName: string = undefined;
    oldPassword: string = undefined;
    remembered: boolean = undefined;
    validateForm(reg) {
      if (!this.username) {
        alert('  .');
        return false;
      };
      if (!this.password) {
        alert(' .');
        return false;
      };
      if (reg && !this.secondPassword) {
        alert(' .');
        return false;
      };
      if (reg && this.password !== this.secondPassword) {
        alert('  .');
        return false;
      };
      return true;
    };
    saveNewUser() {
      if (!this.validateForm(true)) {
        return;
      };
      let data = {
        username: this.username,
        password: this.password,
        firstName: this.firstName,
        surName: this.surName,
        middleName: this.middleName
      };
      for (let prop in data) {
        if (data[prop] === undefined || data[prop] === '') {
          delete data[prop];
        };
      };
      this.$store.dispatch('saveNewUser', data);
    };
    authUser() {
      if (!this.validateForm(false)) {
        return;
      };
      let data = {
        username: this.username,
        password: this.password,
        remembered: this.remembered
      };
      if (this.remembered === undefined) {
        delete data.remembered;
      };
      this.$store.dispatch('authUser', data);
    };
}


