import Vue, { ComponentOptions } from 'vue';
import { Component, watch } from 'vue-property-decorator';
import moment from 'moment';
import './news.scss';

moment.locale('ru');

@Component({
  template: require('./news.html'),
  computed: {
    getNews: function() {
      return this.$store.state.newsList;
    },
    getNewsLength: function() {
      return (this.$store.state.newsList) ? (this.$store.state.newsList.length) ? true : false : false;
    },
    editMode: function() {
      return this.$store.state.newsEditMode;
    },
    getCurrentUser: function() {
      return this.$store.state.user;
    },
    getPermissionC: function() {
      return this.$store.state.user.permission.news.C;
    },
    getPermissionU: function() {
      return this.$store.state.user.permission.news.U;
    },
    getPermissionD: function() {
      return this.$store.state.user.permission.news.D;
    }
  },
  data() {
    return {
      newsauthor: this.$store.state.user.username,
      newsdate: moment(), // .format('DD.MM.YYYY HH:mm')
      newstheme: undefined,
      newstext: undefined,
      edited: undefined
    };
  },
  methods: {
    getDate(date) {
      return moment(date).format('DD.MM.YYYY HH:mm');
    }
  }
})

export class NewsComponent extends Vue {
  newsauthor: string = undefined;
  newsdate: string = undefined;
  newstheme: string = undefined;
  newstext: string = undefined;
  edited: any = undefined;
  created() {
    this.$store.dispatch('loadNews');
  };
  createNews() {
    this.$store.commit('setNewsEditMode', undefined);
  };
  updateNews(e) {
    let newsItem = this.$store.state.newsList.find(item => item.id.toString() === e.target.getAttribute('data-id'));
    this.newsauthor = this.$store.state.user.username;
    this.newsdate = newsItem.date;
    this.newstheme = newsItem.theme;
    this.newstext = newsItem.text;
    this.edited = newsItem;
    this.$store.commit('setNewsEditMode', newsItem);
  };
  deleteNews(e) {
    let newsItem = this.$store.state.newsList.find(item => item.id.toString() === e.target.getAttribute('data-id'));
    this.$store.dispatch('deleteNews', {id: newsItem.id});
  };
  saveNews() {
    let data: any = {
      userId: this.$store.state.user.id,
      text: this.newstext,
      theme: this.newstheme
    };
    data.date = moment(this.newsdate).format('YYYY-MM-DD HH:mm:ssZ');
    if (!this.edited) {
      this.$store.dispatch('saveNewNews', data);
    } else {
      data.id = this.edited.id;
      this.$store.dispatch('updateNews', data);
    };
  };
  resetNews() {
    this.$store.commit('setNewsEditMode', undefined);
  };
}


