let news = {
  id: '',
  text: '',
  theme: '',
  date: '',
  user: {
    access_token: '',
    firstName: '',
    id: '', 
    image: '',
    middleName: '',
    password: '',
    surName: '',
    username: ''
  }
};

let user = {
  access_token: '',
  firstName: '',
  id: '', 
  image: '',
  middleName: '',
  password: '',
  permission: {
    chat: {
      C: 'boolean',
      R: 'boolean',
      U: 'boolean',
      D: 'boolean'
    },
    news: {
      C: 'boolean',
      R: 'boolean',
      U: 'boolean',
      D: 'boolean'
    },
    setting: {
      C: 'boolean',
      R: 'boolean',
      U: 'boolean',
      D: 'boolean'
    }
  },
  permissionId: '',
  surName: '',
  username: ''
};

let actions = {
  loadNews({ commit }) {
    console.log(' GET-  /api/getNews -   ');
    console.log('       !');
    return new Promise((resolve, reject) => {
      return fetch('/api/getNews', {
        method: 'GET'
      })
      .then((response) => {
        return response.json();
      })
      .then(function(data: any) {
        console.log(` : `, data, `;   : `, [news, news]);
        if (data.error) {
          alert(`Error! Type = ${data.error}`);
        } else if (!data) {
          alert(` `);
        } else {
          console.log(`!`);
          commit('setNewsList', data);
        };
      });
    });
  },
  saveNewNews({ commit }, payload) {
    console.log('POST-  /api/newNews -   ');
    console.log(' : ', payload);
    console.log('        !');
    return fetch('/api/newNews/', {
      method: 'POST',
      body: JSON.stringify(payload)
    })
    .then((response) => {
      return response.json();
    })
    .then(function(data: any) {
      console.log(` : `, data, `;   : `, [news, news]);
      if (data.error) {
        alert(`Error! Type = ${data.error}`);
      } else if (!data) {
        alert(` `);
      } else {
        console.log(`!`);
        commit('setNewsList', data);
      };
    });
  },
  updateNews({ commit }, payload) {
    console.log('PUT-  /api/updateNews/:id -   ');
    console.log(' : ', payload);
    console.log('        !');
    return fetch('/api/updateNews/' + payload.id, {
      method: 'PUT',
      body: JSON.stringify(payload)
    })
    .then((response) => {
      return response.json();
    })
    .then(function(data: any) {
      console.log(` : `, data, `;   : `, [news, news]);
      if (data.error) {
        alert(`Error! Type = ${data.error}`);
      } else if (!data) {
        alert(` `);
      } else {
        console.log(`!`);
        commit('setNewsList', data);
      };
    });
  },
  deleteNews({ commit }, payload) {
    console.log('DELETE-  /api/deleteNews/:id -   ');
    console.log(' : ', payload);
    console.log('        !');
    return fetch('/api/deleteNews/' + payload.id, {
      method: 'delete'
    })
    .then((response) => {
      return response.json();
    })
    .then(function(data: any) {
      console.log(` : `, data, `;   : `, [news, news]);
      if (data.error) {
        alert(`Error! Type = ${data.error}`);
        commit('failLoadConfig');
      } else if (!data) {
        alert(` `);
      } else {
        console.log(`!`);
        commit('setNewsList', data);
      };
    });
  },
  loadUsers({ commit }) {
    console.log(' GET-  /api/getUsers -   ');
    console.log('       !');
    return new Promise((resolve, reject) => {
      return fetch('/api/getUsers', {
        method: 'GET'
      })
      .then((response) => {
        return response.json();
      })
      .then(function(data: any) {
        console.log(` : `, data, `;   : `, [user, user]);
        if (data.error) {
          alert(`Error! Type = ${data.error}`);
          commit('failLoadConfig');
        } else if (!data) {
          alert(` `);
        } else {
          console.log(`!`);
          commit('setUserList', data);
        };
      });
    });
  },
  saveUserPermission({ commit }, payload) {
    return new Promise((resolve, reject) => {
      console.log('PUT-  /api/updateUserPermission/:id -     ');
      console.log(' : ', payload);
      return fetch('/api/updateUserPermission/' + payload.permissionId, {
        method: 'PUT',
        body: JSON.stringify(payload)
      })
      .then((response) => {
        return response.json();
      })
      .then(function(data: any) {
        console.log(` : `, data, `;`);
        if (data.error) {
          alert(`Error! Type = ${data.error}`);
          commit('failLoadConfig');
          console.log(`     !`);
        } else if (!data) {
          alert(` `);
        } else {
          console.log(`!`);
        };
      });
    });
  },
  loadUserData({ commit }, payload) {
    console.log(' POST-  /api/authFromToken -    .');
    console.log(' : ', payload);
    console.log('    !');
    fetch('/api/authFromToken', {
      method: 'POST',
      body: JSON.stringify({ access_token: payload })
    })
    .then((response) => {
      if (!response) {
        return;
      }
      return response.json();
    })
    .then(function(data: any) {
      console.log(` : `, data, `;   : `, user);
      if (data.error || !data || !data.access_token) {
        alert(`Error! Type = ${data.error}`);
          commit('setUserData', {
            access_token: undefined,
            user: undefined
          });
        console.log(`     !`);
      } else {
        console.log(`!`);
        commit('setUserData', {
          access_token: data.access_token,
          user: data
        });
      };
    });
  }, 
  authUser({ commit }, payload) {
    console.log('POST-  /api/login -    .');
    console.log(' : ', payload);
    console.log('    !');
    return fetch('/api/login', {
      method: 'POST',
      credentials: 'include',
      body: JSON.stringify(payload)
    })
    .then((response) => {
      return response.json();
    })
    .then(function(data: any) {
      console.log(` : `, data, `;   : `, user);
      if (data.error || !data.access_token) {
        alert(`Error! Type = ${data.error}`);
        commit('failLoadConfig');
        console.log(`     !`);
      } else if (!data) {
        alert(` `);
      } else {
        console.log(`!`);
        commit('setUserData', {
          access_token: data.access_token,
          user: data
        });
      };
    });
  },
  saveUpdatedUser({ commit }, payload) {
    console.log('PUT-  /api/updateUser/:id -    .');
    console.log(' : ', payload);
    console.log('    !');
    return fetch('/api/updateUser/' + payload.id, {
      method: 'PUT',
      body: JSON.stringify(payload)
    })
    .then((response) => {
      return response.json();
    })
    .then(function(data: any) {
      console.log(` : `, data, `;   : `, user);
      if (data.error || !data.access_token) {
        alert(`Error! Type = ${data.error}`);
        commit('failLoadConfig');
        console.log(`     !`);
      } else if (!data) {
        alert(` `);
      } else {
        console.log(`!`);
        commit('setUserData', {
          access_token: data.access_token,
          user: data
        });
      };
    });
  },
  saveUserImage({ commit }, payload) {
    console.log('POST-  /api/saveUserImage/:id -   .');
    let data = new FormData();
    data.append(payload.id, payload.file[0]);
    console.log(' : ', data);
    console.log('     path,      .');
    return fetch('/api/saveUserImage/' + payload.id, {
      method: 'post',
      body: data
    })
    .then((response) => {
      return response.json();
    })
    .then(function(data: any) {
      return data;
    });
  },
  deleteUser({ commit }, payload) {
    console.log('DELETE-  /api/deleteUser/:id -  .');
    console.log(' : ', payload);
    return fetch('/api/deleteUser/' + payload.id, {
      method: 'delete'
    })
    .then((response) => {
      return response.json();
    })
    .then(function(data: any) {
      if (data.error) {
        alert(`Error! Type = ${data.error}`);
        commit('failLoadConfig');
      } else if (!data) {
        alert(` `);
      } else {
        console.log(`!`);
      };
    });
  },
  saveNewUser({ commit }, payload) {
    payload.img = '';
    payload.permission = {
      chat: {
        C: false,
        R: true,
        U: true,
        D: false
      },
      news: {
        C: false,
        R: true,
        U: false,
        D: false
      },
      setting: {
        C: false,
        R: false,
        U: false,
        D: false
      }
    };
    console.log('POST-  /api/saveNewUser -    ().');
    console.log(' : ', payload);
    console.log('    !');
    return fetch('/api/saveNewUser', {
      method: 'POST',
      credentials: 'include',
      body: JSON.stringify(payload)
    })
    .then((response) => {
      return response.json();
    })
    .then(function(data: any) {
      console.log(` : `, data, `;   : `, user);
      if (data.error || !data.access_token) {
        commit('failLoadConfig');
        alert(`   !     .`);
      } else if (!data) {
        alert(` `);
      } else {
        console.log(`!`);
        commit('setUserData', {
          access_token: data.access_token,
          user: data
        });
      };
    });
  }
};

export default actions;



