import Vue, { ComponentOptions } from 'vue';
import { Component, watch } from 'vue-property-decorator';

import './settings.scss';

@Component({
  template: require('./settings.html'),
  data () {
    return {
      user: this.$store.state.user,
      image: this.$store.state.user.image,
      password: undefined,
      secondPassword: undefined,
      firstName: this.$store.state.user.firstName,
      surName: this.$store.state.user.surName,
      middleName: this.$store.state.user.middleName,
      oldPassword: undefined,
      file: undefined
    };
  }
})

export class SettingsComponent extends Vue {
  user: any = this.$store.state.user;
  image: any = this.$store.state.user;
  password: any = undefined;
  secondPassword: any = undefined;
  firstName: any = this.$store.state.user.firstName;
  surName: any = this.$store.state.user.surName;
  middleName: any = this.$store.state.user.middleName;
  oldPassword: any = undefined;
  file: any = undefined;
  fileInput(file) {
    this.file = file;
  };
  saveChanges() {
    let data: any = {};
    if (this.firstName !== this.user.firstName) {
      data.firstName = this.firstName;
    };
    if (this.middleName !== this.user.middleName) {
      data.middleName = this.middleName;
    };
    if (this.surName !== this.user.surName) {
      data.surName = this.surName;
    };
    if (this.image !== this.user.image) {
      data.image = this.image;
    };
    if (this.oldPassword && this.password && this.secondPassword) {
      if (this.password !== this.secondPassword) {
        return alert('   .');
      };
      data.oldPassword = this.oldPassword;
      data.password = this.password;
    };
    if (!Object.keys(data).length && !this.file) {
      return alert(' .');
    };
    data.id = this.user.id;
    let self = this;
    if (this.file) {
      let path = this.$store.dispatch('saveUserImage', {id: data.id, file: this.file});
      path.then(function(response) {
        data.image = response.path;
        self.$store.dispatch('saveUpdatedUser', data);
      });
    } else {
      this.$store.dispatch('saveUpdatedUser', data);
    };
  };
};



