import { getCookie } from '../util/helpers';

class Link {
    name: string;
    path: string;
    inMenu: boolean;
    key: string;

    constructor(name: string, path: string, inMenu: boolean, key: string) {
        this.name = name;
        this.path = path;
        this.inMenu = inMenu;
        this.key = key;
    }
};

let state = {
  links: [
    new Link('', '/', true, 'main'),
    new Link('', '/news', true, 'news'),
    new Link('', '/admin_panel', false, 'setting'),
    new Link('', '/chat', false, 'chat')
  ],
  config: undefined,
  configError: undefined,
  access_token: getCookie('access_token'),
  user: undefined,
  userList: undefined,
  newsList: undefined,
  isRegNeeded: false,
  newsEditMode: false,
  newsToEdit: {}
};

export default state;



