import Vue, { ComponentOptions } from 'vue';
import VueRouter, { RouteConfig } from 'vue-router';
import VueMaterial from 'vue-material';

import store from './store';
import components, { ComponentConfig } from './components';

import { HeaderComponent } from './components/header';
import { MainComponent } from './components/Main';

import './sass/vue-material.scss';
import './sass/main.scss';

Vue.use(VueRouter);
Vue.use(VueMaterial);

let defaultPath: string = components[0].path;

let routes: RouteConfig[] = components.map((componentConf) => {
  if (componentConf.default) {
    defaultPath = componentConf.path;
  };
  let { path, component } = componentConf;
  return <RouteConfig>{ path, component };
});

let router = new VueRouter({
  mode   : 'history',
  routes : routes.concat([
    <RouteConfig>{
      path     : '*',
      redirect : defaultPath
    }
  ])
});

let vm = new Vue({
  el: '#app-main',
  router: router,
  store: store,
  components: {
    'header-bar': HeaderComponent,
    'main-component': MainComponent
  },
  computed: {
    getConfig() {
      return this.$store.state.config === undefined;
    },
    getUser() {
      return this.$store.state.user;
    }
  },
  created() {
    let access_token = this.$store.state.access_token,
    user = this.$store.state.user;
    if (access_token && access_token !== 'undefined' && user === undefined) {
      this.$store.dispatch('loadUserData', access_token);
    };
  }
});



