import Vue, { ComponentOptions } from 'vue';
import { Component, watch } from 'vue-property-decorator';

import './adminPanel.scss';

@Component({
  template: require('./adminPanel.html'),
  computed: {
    getUsers: function() {
      return this.$store.state.userList;
    }
  },
  data() {
    return {
      selected: undefined,
      selectedUser: undefined,
      selectedUser__setting_c: undefined,
      selectedUser__setting_r: undefined,
      selectedUser__setting_u: undefined,
      selectedUser__setting_d: undefined,
      selectedUser__chat_c: undefined,
      selectedUser__chat_r: undefined,
      selectedUser__chat_u: undefined,
      selectedUser__chat_d: undefined,
      selectedUser__news_c: undefined,
      selectedUser__news_r: undefined,
      selectedUser__news_u: undefined,
      selectedUser__news_d: undefined,
    };
  },
})

export class AdminPanelComponent extends Vue {
  selectedUser: any = undefined;
  selected: any = undefined;
  selectedUser__setting_c: boolean = undefined;
  selectedUser__setting_r: boolean = undefined;
  selectedUser__setting_u: boolean = undefined;
  selectedUser__setting_d: boolean = undefined;
  selectedUser__chat_c: boolean = undefined;
  selectedUser__chat_r: boolean = undefined;
  selectedUser__chat_u: boolean = undefined;
  selectedUser__chat_d: boolean = undefined;
  selectedUser__news_c: boolean = undefined;
  selectedUser__news_r: boolean = undefined;
  selectedUser__news_u: boolean = undefined;
  selectedUser__news_d: boolean = undefined;
  created() {
    this.$store.dispatch('loadUsers');
  };
  setSelectedUser(value) {
    let selected = this.$store.state.userList.find(item => item.id === value);
    this.selected = selected;
    this.selectedUser__setting_c = selected.permission.setting.C;
    this.selectedUser__setting_r = selected.permission.setting.R;
    this.selectedUser__setting_u = selected.permission.setting.U;
    this.selectedUser__setting_d = selected.permission.setting.D;
    this.selectedUser__chat_c = selected.permission.chat.C;
    this.selectedUser__chat_r = selected.permission.chat.R;
    this.selectedUser__chat_u = selected.permission.chat.U;
    this.selectedUser__chat_d = selected.permission.chat.D;
    this.selectedUser__news_c = selected.permission.news.C;
    this.selectedUser__news_r = selected.permission.news.R;
    this.selectedUser__news_u = selected.permission.news.U;
    this.selectedUser__news_d = selected.permission.news.D;
  };
  save() {
    let permission: any = {
      chat: {
        id: this.selected.permission.chat.id,
        C: undefined,
        R: undefined,
        U: undefined,
        D: undefined
      },
      news: {
        id: this.selected.permission.news.id,
        C: undefined,
        R: undefined,
        U: undefined,
        D: undefined
      },
      setting: {
        id: this.selected.permission.setting.id,
        C: undefined,
        R: undefined,
        U: undefined,
        D: undefined
      }
    };
    (this.selectedUser__setting_c !== this.selected.permission.setting.C) ? permission.setting.C = this.selectedUser__setting_c : null;
    (this.selectedUser__setting_r !== this.selected.permission.setting.R) ? permission.setting.R = this.selectedUser__setting_r : null;
    (this.selectedUser__setting_u !== this.selected.permission.setting.U) ? permission.setting.U = this.selectedUser__setting_u : null;
    (this.selectedUser__setting_d !== this.selected.permission.setting.D) ? permission.setting.D = this.selectedUser__setting_d : null;
    (this.selectedUser__chat_c !== this.selected.permission.chat.C) ? permission.chat.C = this.selectedUser__chat_c : null;
    (this.selectedUser__chat_r !== this.selected.permission.chat.R) ? permission.chat.R = this.selectedUser__chat_r : null;
    (this.selectedUser__chat_u !== this.selected.permission.chat.U) ? permission.chat.U = this.selectedUser__chat_u : null;
    (this.selectedUser__chat_d !== this.selected.permission.chat.D) ? permission.chat.D = this.selectedUser__chat_d : null;
    (this.selectedUser__news_c !== this.selected.permission.news.C) ? permission.news.C = this.selectedUser__news_c : null;
    (this.selectedUser__news_r !== this.selected.permission.news.R) ? permission.news.R = this.selectedUser__news_r : null;
    (this.selectedUser__news_u !== this.selected.permission.news.U) ? permission.news.U = this.selectedUser__news_u : null;
    (this.selectedUser__news_d !== this.selected.permission.news.D) ? permission.news.D = this.selectedUser__news_d : null;
    for (let prop in permission) {
      for (let lastProp in permission[prop]) {
        if (lastProp === 'id') {
          continue;
        };
        if (permission[prop][lastProp] === null || permission[prop][lastProp] === undefined) {
          delete permission[prop][lastProp];
        };
      };
      if (Object.keys(permission[prop]).length === 1) {
        delete permission[prop];
      };
    };
    if (!Object.keys(permission).length) {
      return alert(' ');
    };
    let data = {
      permissionId: this.selected.permissionId,
      permission: permission
    };
    this.$store.dispatch('saveUserPermission', data);
  };
  deleteUser() {
    this.$store.dispatch('deleteUser', {id: this.selected.id});
  }
  reset() {
    this.selectedUser__setting_c = this.selected.permission.setting.C;
    this.selectedUser__setting_r = this.selected.permission.setting.R;
    this.selectedUser__setting_u = this.selected.permission.setting.U;
    this.selectedUser__setting_d = this.selected.permission.setting.D;
    this.selectedUser__chat_c = this.selected.permission.chat.C;
    this.selectedUser__chat_r = this.selected.permission.chat.R;
    this.selectedUser__chat_u = this.selected.permission.chat.U;
    this.selectedUser__chat_d = this.selected.permission.chat.D;
    this.selectedUser__news_c = this.selected.permission.news.C;
    this.selectedUser__news_r = this.selected.permission.news.R;
    this.selectedUser__news_u = this.selected.permission.news.U;
    this.selectedUser__news_d = this.selected.permission.news.D;
  };
};



