import { deleteCookie } from '../util/helpers';

let mutations = {
  getConfig(state, payload) {
    state.config = payload;
  },
  failLoadConfig(state, payload) {
    state.configError = payload;
  },
  setNewsEditMode(state, payload) {
    state.newsEditMode = !state.newsEditMode;
    state.newsToEdit = (payload) ? payload : {};
  },
  setUserData(state, payload) {
    state.access_token = payload.access_token;
    state.user = payload.user;
  },
  logout(state, payload) {
    state.access_token = undefined;
    state.user = {};
    deleteCookie('access_token');
  },
  setNewsList(state, payload) {
    state.newsEditMode = false;
    state.newsList = payload;
  },
  setUserList(state, payload) {
    state.userList = payload;
  },
  toggleMode(state) {
    state.isRegNeeded = !state.isRegNeeded;
  }
};

export default mutations;



